}

.button {
  position: relative;
  display: block;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  border: 0;
  transition: opacity 0.3s ease;
  cursor: pointer;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.button.focus-visible:focus {
  opacity: 0.8;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.button--toggle {
  width: 36px;
  height: 40px;
}
.button--toggle::before, .button--toggle::after {
  position: absolute;
  left: 0;
  content: "";
  width: 36px;
  height: 2px;
  background-color: #303030;
  transition: transform 0.3s ease, background-color 0.3s ease;
}
.button--toggle::before {
  top: 13px;
}
.button--toggle::after {
  bottom: 13px;
}
.button--toggle.is-active::before, .button--toggle.is-active::after {
  background-color: #ffffff;
}
.button--toggle.is-active::before {
  transform: translate3d(0, 5px, 0) rotate(-45deg);
}
.button--toggle.is-active::after {
  transform: translate3d(0, -6px, 0) rotate(45deg);
}
@media (hover: hover) {
  .button--toggle:hover:not(.focus-visible) {
    opacity: 1;
  }
  .button--toggle:hover:not(.focus-visible)::before {
    transform: translate3d(-4px, 0, 0);
  }
  .button--toggle:hover:not(.focus-visible)::after {
    transform: translate3d(4px, 0, 0);
  }
  .button--toggle:hover:not(.focus-visible).is-active::before {
    transform: translate3d(0, 5px, 0) rotate(-39deg);
  }
  .button--toggle:hover:not(.focus-visible).is-active::after {
    transform: translate3d(0, -6px, 0) rotate(39deg);
  }
}
.button--toggle.focus-visible:focus {
  opacity: 1;
}
.button--toggle.focus-visible:focus::before {
  transform: translate3d(-4px, 0, 0);
}
.button--toggle.focus-visible:focus::after {
  transform: translate3d(4px, 0, 0);
}
.button--toggle.focus-visible:focus.is-active::before {
  transform: translate3d(0, 5px, 0) rotate(-39deg);
}
.button--toggle.focus-visible:focus.is-active::after {
  transform: translate3d(0, -6px, 0) rotate(39deg);
}

.link {
  display: block;
}
