  position: sticky;
  top: 0;
  min-height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}

.scroll-slide {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  max-height: 100vh;
  /* stylelint-disable declaration-block-no-duplicate-properties */
  min-height: calc(100 * var(--vh, 1vh));
  max-height: calc(100 * var(--vh, 1vh));
  /* stylelint-enable declaration-block-no-duplicate-properties */
  opacity: 0;
  pointer-events: none;
  transform: translate3d(0, 0, 0);
  transition: 0.3s ease;
}
.scroll-slide.is-active {
  opacity: 1;
  pointer-events: all;
}
.scroll-slide .container {
  z-index: 2;
  position: relative;
}
.scroll-slide .content-block {
  position: relative;
  max-width: 70%;
  margin: 0 auto;
  transform: translate3d(0, 0, 0);
}
@media (max-width: 767px) {
  .scroll-slide .content-block {
    max-width: 100%;
  }
}
.scroll-slide .content-block::before {
  z-index: -1;
  position: absolute;
  top: -50%;
  right: -10%;
  bottom: -30%;
  left: -10%;
  content: "";
  width: 120%;
  height: 200%;
  background-color: #ffffff;
  border-radius: 50%;
  opacity: 0.6;
  filter: blur(70px);
}
.scroll-slide__bg {
  z-index: 1;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  color: #f6f6f6;
  background-repeat: repeat;
  transform: translate3d(0, 0, 0);
}

.loader {
  z-index: 1000;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #303030;
}
.loader.is-hidden {
  display: none;
}
.loader__box {
  position: absolute;
  top: calc(50% - 20px);
  left: calc(50% - 20px);
  width: 40px;
  height: 40px;
  transform-origin: center;
  animation-name: rotate-360;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.loader__box-item {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #ffffff;
  border-radius: 50%;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}
.loader__box-item:nth-child(1) {
  top: 0;
  left: 0;
  animation-name: top-to-bottom;
}
.loader__box-item:nth-child(2) {
  bottom: 0;
  left: 0;
  animation-name: left-to-right;
}
.loader__box-item:nth-child(3) {
  right: 0;
  bottom: 0;
  animation-name: bottom-to-top;
}
.loader__box-item:nth-child(4) {
  top: 0;
  right: 0;
  animation-name: right-to-left;
}

.ya-map {
  padding: calc(var(--header-height) + 50px) 0 100vh;
}
.ya-map__title {
  margin-bottom: 60px;
}
.ya-map__inner {
  position: relative;
  display: flex;
  width: 100%;
  height: 700px;
  margin-top: 100px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.4);
}
.ya-map__map {
  width: 100%;
  height: 100%;
}
.ya-map__message {
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.3);
  transform: translate3d(-50%, -50%, 0);
  transition: opacity 0.3s ease;
  opacity: 0;
  pointer-events: none;
}
.ya-map__message.is-active {
  opacity: 1;
  pointer-events: all;
}

.map-pin {
  position: relative;
  transform: translate3d(0, 0, 0);
}
.map-pin svg {
  filter: drop-shadow(4px 4px 8px rgba(34, 60, 80, 0.2));
}

.placemark {
  z-index: 1;
  position: relative;
  width: 48px;
  height: 48px;
  outline: 0;
  transform: translate3d(0, 0, 0);
  transition: z-index 0.3s ease, transform 0.3s ease, visibility 0.3s ease;
}
.placemark__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background-color: #6bcaff;
  border-radius: 50%;
}

.placemark-balloon {
  z-index: 3;
  position: relative;
  display: flex;
  width: max-content;
  min-height: 100px;
  max-width: 400px;
  border-radius: 20px;
  box-shadow: 8px 8px 13px 1px rgba(34, 60, 80, 0.2);
  transform: translateX(-50%);
  transition: opacity 0.6s ease, visibility 0.6s ease, transform 0.6s ease;
  overflow: hidden;
}
.placemark-balloon__inner {
  display: flex;
  height: 100%;
  background-color: #ffffff;
}
.placemark-balloon__image {
  display: flex;
  flex-shrink: 0;
  width: 100px;
  height: 100px;
}
.placemark-balloon__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.placemark-balloon__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px 20px 10px 10px;
}
.placemark-balloon__head {
  margin: 0 0 10px;
  font-size: 10px;
  line-height: 1.4;
  color: #aaa5a0;
}
.placemark-balloon__title {
  margin: 0 0 5px;
  font-size: 14px;
  line-height: 1.2857142857;
  font-weight: 600;
}
.placemark-balloon__text {
  margin: 0;
  font-size: 10px;
  line-height: 1.4;
}
.placemark-balloon__close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  border: 0;
  cursor: pointer;
}
.placemark-balloon__close-btn svg {
  fill: #000000;
}

.map-filter {
  padding: 50px 0 0;
}
.map-filter__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: -10px -15px 0;
  padding: 0;
  list-style: none;
}
.map-filter__item {
  padding: 10px 15px 0;
}
.map-filter input:checked + .map-filter__label {
  background-color: #6bcaff;
}
.map-filter input:checked + .map-filter__label svg {
  transform: scale(1.15);
}
@media (hover: hover) {
  .map-filter input:checked:hover:not(.focus-visible) + .map-filter__label {
    opacity: 1;
  }
}
.map-filter input:checked.focus-visible:focus + .map-filter__label {
  border-color: #0032ff;
  opacity: 1;
}
.map-filter__label {
  display: flex;
  align-items: center;
  padding: 10px 20px 10px 5px;
  font-size: 18px;
  line-height: 1.1666666667;
  background-color: rgba(107, 202, 255, 0.5);
  border: 2px solid rgba(255, 255, 255, 0);
  border-radius: 15px;
  transition: opacity 0.3s ease;
  user-select: none;
  cursor: pointer;
}
.map-filter__label svg {
  margin: 0 10px 0 0;
  transition: transform 0.3s ease;
}
@media (hover: hover) {
  .map-filter__label:hover:not(.focus-visible) {
    opacity: 0.65;
  }
  .map-filter__label:hover:not(.focus-visible) svg {
    transform: scale(1.15);
  }
}
.map-filter__label.focus-visible:focus {
  opacity: 0.65;
}
.map-filter__label.focus-visible:focus svg {
  transform: scale(1.15);
}

.cluster-balloon {
  display: flex;
  flex-direction: column;
}
.cluster-balloon__wrap {
  padding: 10px;
}
.cluster-balloon__head {
  margin: 0 0 10px;
  font-size: 10px;
  line-height: 1.4;
  text-align: right;
  color: #aaa5a0;
}
.cluster-balloon__title {
  margin: 0 0 5px;
  font-size: 14px;
  line-height: 1.2857142857;
  font-weight: 600;
}
.cluster-balloon__text {
  margin: 0;
  font-size: 10px;
  line-height: 1.4;
}
.cluster-balloon__image {
  display: flex;
  border-radius: 10px;
  overflow: hidden;
}
.cluster-balloon__image img {
  width: 100%;
  height: 80px;
  object-fit: cover;
}

